body{
    background-color: #8B0000;
    padding: 20px;
    margin: 0;
}
h1, h2, p, ul, li {
    font-family: sans-serif;
}
h1{
    text-align: center;
   text-decoration-style: double;
    font-style: italic;
}
h1,h2{
    text-decoration: underline;
}
ul.header li {
    display: inline;
    list-style-type: none;
    margin: 0;
}
ul.header {
    background-color: #111;
    padding: 0;
}
ul.header li a {
    color: #FFF;
    font-weight: bold;
    font-family: sans-serif;
    text-decoration: none;
    padding: 20px;
    display: inline-block;
}
.content {
    background-color: #cccccc;
    padding: 20px;
}
.content h2 {
    padding: 0;
    margin: 0;
}
.content li {
    margin-bottom: 10px;
}
.active {
    background-color: #cccccc;
}
